import { WebpackAsyncRoute } from '@angularclass/webpack-toolkit';
import { RouterConfig } from '@angular/router';
import { Home } from './home';
import { Mission } from './mission';
import { Events } from './events';
import { Contact } from './contact';
import { NoContent } from './no-content';

import { DataResolver } from './app.resolver';

export const routes: RouterConfig = [
  { path: '',      component: Home },
  { path: 'home',  component: Home },
  { path: 'mission', component: Mission },
  { path: 'events',  component: Events },
  { path: 'contact',  component: Contact },

  // make sure you match the component type string to the require in asyncRoutes
  { path: 'about', component: 'About',
    resolve: {
      'yourData': DataResolver
    }
  },
  // async components with children routes must use WebpackAsyncRoute
  { path: 'sermons', component: 'Sermons',
    canActivate: [ WebpackAsyncRoute ],
    children: [
      { path: '', component: 'Index' },
      { path: 'outline', component: 'Outline' },
      { path: 'reading', component: 'Reading' },
      { path: 'podcast', component: 'Podcast' }   // must be included
    ]},
  { path: 'resources', component: 'Resources',
    canActivate: [ WebpackAsyncRoute ],
    children: [
      { path: '', component: 'Index' },
      { path: 'books', component: 'Books' },
      { path: 'physvideo', component: 'PhysicalVideo' },
      { path: 'qanda', component: 'QandA' }   // must be included
    ]},
  { path: '**',    component: NoContent },
];

// Async load a component using Webpack's require with es6-promise-loader and webpack `require`
// asyncRoutes is needed for our @angularclass/webpack-toolkit that will allow us to resolve
// the component correctly

export const asyncRoutes: AsyncRoutes = {
  // we have to use the alternative syntax for es6-promise-loader to grab the routes
  'About': require('es6-promise-loader!./about'),
  'Sermons': require('es6-promise-loader!./+sermons'),
  'SIndex': require('es6-promise-loader!./+sermons'), // must be exported with detail/index.ts
  'Reading': require('es6-promise-loader!./+sermons'), // must be exported with detail/index.ts
  'Outline': require('es6-promise-loader!./+sermons'), // must be exported with detail/index.ts
  'Podcast': require('es6-promise-loader!./+sermons'), // must be exported with detail/index.ts
  'Resources': require('es6-promise-loader!./+resources'),
  'RIndex': require('es6-promise-loader!./+resources'), // must be exported with detail/index.ts
  'PhysicalVideo': require('es6-promise-loader!./+resources'), // must be exported with detail/index.ts
  'Books': require('es6-promise-loader!./+resources'), // must be exported with detail/index.ts
  'QandA': require('es6-promise-loader!./+resources'), // must be exported with detail/index.ts
};


// Optimizations for initial loads
// An array of callbacks to be invoked after bootstrap to prefetch async routes
export const prefetchRouteCallbacks: Array<IdleCallbacks> = [
  asyncRoutes['About'],
  asyncRoutes['Sermons'],
  asyncRoutes['Resources']
   // es6-promise-loader returns a function
];


// Es6PromiseLoader and AsyncRoutes interfaces are defined in custom-typings



